/* https://github.com/fylgja/fylgja-flex-grid */

.flex-grid {
    display: flex;
    flex-flow: row wrap;
    align-items: stretch;
    margin-right: calc(var(--gutter) * -1  / 2);
    margin-left: calc(var(--gutter) * -1 / 2);
}

.flex-grid>.cell {
    margin-bottom: var(--gutter);
    padding-right: calc(var(--gutter) / 2);
    padding-left: calc(var(--gutter) / 2);
    flex: 0 0 auto;
    width: 100%;
}

.flex-grid>.cell.xxs-1 {
    flex: 0 0 auto;
    width: 100%
}

.flex-grid>.cell.xxs-2 {
    flex: 0 0 auto;
    width: calc(100% / 2)
}

.flex-grid>.cell.xxs-grow {
    flex: 1 1 auto;
    width: auto
}

.flex-grid>.cell.xxs-shrink {
    flex: 0 0 auto;
    width: auto
}

.flex-grid>.cell.mb-0 {
    margin-bottom: 0;
}

