    .flex-grid>.cell.xs-1 {
        flex: 0 0 auto;
        width: 100%
    }

    .flex-grid>.cell.xs-2 {
        flex: 0 0 auto;
        width: calc(100% / 2)
    }

    .flex-grid>.cell.xs-3 {
        flex: 0 0 auto;
        width: calc(100% / 3)
    }

    .flex-grid>.cell.xs-3-2 {
        flex: 0 0 auto;
        width: calc((100% / 3) * 2)
